import React, {Component, Suspense} from 'react';
import APISerivce from '../../services/ApiService';
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
  CSelect,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react';


class UserEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
          id: '',
          router_amount: 0,
          status: '',
        }
    }
    componentDidMount() {
      const { params } = this.props.match;
      const {id} = params
      APISerivce.get(`/listroutercharge/${id}`).then(res=>{
        const { router_amount, status} = res.data;
        this.setState({
          router_amount, 
          status,
          id
        })
      }).catch(err=>{
        console.log('err',err)
      })
    }
    handleCancel = ()=> {
        this.props.history.push('/routercharges')
    }

    handleSubmit = () => {
      const { id, router_amount, status } = this.state;
      if(!!router_amount && !!status) {
        APISerivce.put(`/modifyroutercharges/${id}`,{
          router_amount,
          status,
          user_id:1
        }).then(res=>{
          this.props.history.push('/routercharges');
        }).catch(err=>{
          console.log('err',err)
          alert(`Error try again`)
        })
      }
      
    }

    handleUserChange = event => {
        const { name, value } = event.target;
        this.setState({
          [name]: value
        })
    }


    render() {
        const { router_amount, status } = this.state;
        return (
            <>
              <Suspense fallback={<div>Loading...</div>}>
              <CRow>
                <CCol xs="12" md="10">
                  <CCard>
                    <CCardHeader>
                      Customer Add
                      <small> Form</small>
                    </CCardHeader>
                    <CCardBody>
                      <CForm action="" method="post" className="form-horizontal">
                      <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="router_amount">Router  Amount</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CInput type="text" value={router_amount} onChange={this.handleUserChange} id="router-amount" name="router_amount" placeholder="Enter installation amount" />
                          </CCol>
                        </CFormGroup>              
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="status">Status</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CSelect custom name="status" onChange={this.handleUserChange} value={status} id="status">
                                <option value="">Please select Status</option>
                                <option value="1">Active</option>
                                <option value="0">In-Active</option>
                            </CSelect>
                          </CCol>
                        </CFormGroup>
                      </CForm>
                    </CCardBody>
                    <CCardFooter>
                      <CButton type="reset" size="sm" color="danger" onClick={this.handleCancel}><CIcon name="cil-ban"  /> Cancel</CButton>
                      &nbsp;
                      <CButton type="submit" size="sm" color="primary" onClick={this.handleSubmit}><CIcon name="cil-scrubber" /> Update</CButton> 
                    </CCardFooter>
                  </CCard>
                </CCol>
              </CRow>
              </Suspense>
            </>
          )
    }
}

export default UserEdit
